body, html, #root, .app, .container {
    width: 100%;
    height: 100%;
}

.container {
    font-size: 28px;
    color: #fff;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: #282c34;
}

.loginform {
    box-sizing: border-box;
    width: 100%;
    max-width: 400px;
    padding-right: 20px;
    padding-left: 20px;
}

.inputfield {
    padding-top: 16px;
}
.left {
    margin-left:1100px;
    margin-top:100px;
    font-size:20px;
}

.input {
    box-sizing: border-box;
    outline: none;
    border: solid 2px #1189de;
    border-radius: 4px;
    color: #292929;
    width: 100%;
    padding: 12px;
    font-size: 14px;
    background: rgba(255,255,255,1);
}

.submitform {
    padding-top: 16px;
}

.btn {
    width: 100%;
    min-width: 280px;
    color: #565656;
    padding: 12px;
    font-size: 14px;
    font-weight: bold;
    border: solid 2px #1189de;
    border-radius: 4px;
    background: #fff;
    cursor: pointer;
}
