.card-container{
    width:300px;
    overflow:hidden;
    box-shadow:0px 0px 15px -5px;
    font-size:20px;
    display:inline;
}

.card-content{
    margin:1rem;
    margin-top:0.5rem;
}

h3,p{
    margin:0;
    padding:0;
}

.card-title{
    margin-bottom:0.5rem;
}

.btn{
    display:flex;
    justify-content:center;

}

.btn button{
    padding:0.5rem;
    background-color:white;
    border:none;
    transition:0.2s;
    margin-bottom:0.5rem;
    border-radius:3px;
}

btn button:hover{
    background:rgba(27,156,252,0.1);
}

a{
    text-transform:uppercase;
    color:#189cfc;
    text-decoration:none;
    font-weight:bold;
}